  import React from 'react';
import { storiesOf } from '@storybook/react';
import README from '@availity/avatar/README.md';
import Avatar from '@availity/avatar';
import { withKnobs, text } from '@storybook/addon-knobs/react';

storiesOf('Components|Avatar', module).addDecorator(withStorySource(__STORY__, __ADDS_MAP__))
  .addParameters({
    readme: {
      // Show readme at the addons panel
      sidebar: README,
      // eslint-disable-next-line react/prop-types
      StoryPreview: ({ children }) => <div>{children}</div>,
    },
  })
  .addDecorator(withKnobs)
  .add('default', () => (
    <div className="py-3">
      <Avatar
        fallback={text(
          'Fallback',
          '/public/apps/my-profile/images/Avatars-00.png'
        )}
        skeletonProps={{
          width: text('Skeleton Width', '350px'),
          height: text('Skeleton Height', '350px'),
        }}
      />
    </div>
  ));

  